@import url(https://fonts.googleapis.com/css?family=Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html,body{padding:0;margin:0;background:whitesmoke;font-family:"Rubik";font-size:15px}#shots{display:flex;flex-wrap:wrap;max-width:1080px;margin:auto}#shots .shot{position:relative;display:block;width:25%;background:#ea4c89;color:white;text-decoration:none}#shots .shot:hover img{opacity:.1}#shots .shot:hover .title{opacity:1}#shots .shot .title{position:absolute;width:calc(100% - 2rem);padding:1rem;opacity:0;transition:opacity .2s ease-in-out;font-size:1rem;font-weight:500;letter-spacing:1px}#shots .shot img{display:block;max-width:100%;transition:opacity .2s ease-in-out}

